import { Base } from "../Base"
import { MediaUtilized } from "../Decorators/MediaUtilized"
import { Media } from "../Media/Media"

@MediaUtilized
class Clip extends Base {
  id : string

  frame : number

  frames : number

  scaler : object

  merger : object

  effects : object[]

  from? : object

  to? : object

  media : Media

  mediaUtilized : Media[]

  constructor(object) {
    super(object)
    this.media.initializeInstance(this, object)
  }
}

export { Clip }
